

.fade-enter-active,
.fade-leave-active {
    transition: all .5s ease-in-out;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
  backdrop-filter: blur(0px);
}

.modal-overlay{
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    overflow: auto;
    z-index: 260;
    background-color: rgba(0,0,0,0.3);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    display: flex;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 767px){
        align-items: stretch;
    }

}

.modal{
    font-family: Raleway;
    width: 756px;
    // max-height: 981px;
    background-color: #fff;
    padding: 20px;
    box-sizing: border-box;
    position: relative;
    //trick for centering if content is bigger than screen
    margin: auto;
}
.close {
    background-image: url("../../assets/images/menu-close.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100%;
    width: 26px;
    height: 26px;
    position: absolute;
    right: 10px;
    top: 10px;
    filter: brightness(0);
    &:hover {
        cursor: pointer;
    }
  }
