


  .title-skew-line{
    height: 83px;
    background: #f8f5f4;
    transform: skew(-18deg);
    color: black;
    align-items: center;
    display:flex;
    justify-content: flex-start;

    @media screen and (min-width:768px) and (max-width:1280px){
        height: 44px;
    }

    @media screen and (max-width:767px){
      height: 33px;
      // justify-content: flex-end;

    }

    span{
      transform: skew(18deg);
      display: inline-block;
      font-family: Oswald;
      font-style: normal;
      font-weight: 600;
      font-size: 54px;
      letter-spacing: 0.02em;
      color: #000000;

       @media screen and (min-width:768px) and (max-width:1280px){
          font-size: 37px;
       }

      @media screen and (max-width:767px){
       font-size: 24px;
     }
    }
  }

  .title-skew-line-left{
    @extend .title-skew-line;
        margin-left: 91px; //dynamic
        margin-bottom: 30px; //dynamic
        margin-right: -170px;
        padding-left: 50px;

        @media screen and (max-width:767px){
              margin-left: unset;
              padding-left: 20px;
              // padding-right: 20px;
        }

        // span{
        //   margin-left: 50px
        // }
  }
  .title-skew-line-right{
    @extend .title-skew-line;
    margin-left: -170px;
    justify-content: flex-end;
    margin-right: 120px;
    padding-right: 30px;

        @media screen and (max-width:767px){
              margin-right: 14px;
              padding-right: 20px;
        }

        // span{
        //   margin-left: 50px
        // }
  }

    .view-main-g{
        overflow: hidden;
        background: #FFFFFF;
    }


  .link-1{
    font-family: Raleway;
    font-weight: bold;
    font-size: 12px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-decoration: none;
    color: #000;

    display: flex;
    align-items: center;

    &:active{
      color: #916C58;
    }

    &:after{
      content: '';
      width: 31px;
      height:16px;
      background: url("../../assets/homeView/arrow-right.svg") no-repeat;
      margin-left: 15px;
    }
  }


      .link-2{
        font-family: Raleway;
        font-weight: 800;
        font-size: 18px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        text-decoration:none;
        width: max-content;
        color:#fff;
        padding: 3px;
        transition: all 0.2s linear;
        position: relative;
        cursor: pointer;
        z-index: 1;

        @media screen and (min-width:768px) and (max-width:1280px){
            font-size: 16px;
        }

        @media screen and (max-width:767px){
          font-size: 14px;
        }

        &:before{
            content: "";
            position: absolute;
            bottom: 0;
            left: -8px;
            width: calc(100% + 16px);
            height: 50%;
            background: #916C58;
            z-index: -1;
            transition: all 0.2s linear;
        }
        &:hover:before{
            height: 100%;
        }
        &:active{
            color: #000;
        }
    }


.top-image-container{
  position: relative;

  .top-image{
    display: block;
    width: 100%;
    height: 100vh;
    // width: 100%;
    // display: block;
    // object-fit: cover;


    // @media screen and (max-width:767px){
    //   height: 80vh;
    //   content: url('../../assets/images/main-g-background-768.jpg');
    // }

  }

  .skew-image-1{
    position: absolute;
    bottom: -27px;
    left: 100px;


    @media screen and (min-width:768px) and (max-width:1280px){
        width: 37px;
        bottom: -15px;
    }

    @media screen and (max-width:767px){
      bottom: -11px;
      left: 15px;
      width: 30px;
    }
  }

  .water-text{
    position: absolute;
    font-family: Raleway;
    font-style: normal;
    font-weight: 900;
    font-size: 144px;
    // line-height: 8.802vw;
    text-align: right;
    bottom: -140px;
    right: 7.8125vw;
    color: transparent;
    z-index: 1;


     @media screen and (min-width:768px) and (max-width:1280px){
        font-size: 77px;
        bottom: -80px;
     }

     @media screen and (max-width:767px){
        font-size: 36px;
        bottom: -55px;
     }

     .top-text {
        margin: 0;
        -webkit-text-stroke-color: #fff;
        -webkit-text-stroke-width: 0.5px;
    }
     .down-text {
        margin: 0;
        -webkit-text-stroke-color: #916C58;
        -webkit-text-stroke-width: 1px;

        @media screen and (max-width:767px){
           margin-top: 25px;
        }
    }
  }
  .vector1 {
    position: absolute;
    width: 66.085vw; //1268.85px;
    height: 7.515vw; //144.29px;
    right: -35.9375vw; //-690px;
    bottom: 26.594vw; //510.62px;
    background: linear-gradient(172.97deg, rgba(255, 255, 255, 0.25) 2.64%, rgba(255, 255, 255, 0) 48.7%);
    transform: rotate(-75.49deg);
  }
  .vector2 {
    position: absolute;
    width: 66.085vw; //1268.85px;
    height: 3.705vw; //71.145px;
    right: -28.828vw; //-553.5px;
    bottom: 23.365vw; //448.62px;
    background: linear-gradient(172.97deg, rgba(255, 255, 255, 0.25) 2.64%, rgba(255, 255, 255, 0) 48.7%);
    transform: rotate(-75.49deg);
  }
  .vector3 {
    position: absolute;
    width: 66.085vw; //1268.85px;
    height: 3.705vw; //71.145px;
    right: -24.01vw; //-461px;
    bottom: 19.198vw; //368.62px;
    background: linear-gradient(172.97deg, rgba(255, 255, 255, 0.25) 2.64%, rgba(255, 255, 255, 0) 48.7%);
    transform: rotate(-75.49deg);
  }
}

.about-us{
  background-color: #ffff;
  padding: 69px 150px 60px;
  display: flex;
  flex-direction: column;




    @media screen and (min-width: 768px) and (max-width: 1280px) {
      padding: 45px 50px 30px;

        // .columns-container{
        //     grid-column-gap: 15px;
        //     font-size: 16px;
        //     line-height: 26px;

        //     .col-1 > div,.col-2 > div {
        //         margin-bottom: 15px;
        //     }
        // }
    }


    @media screen and (max-width:767px){
        padding: 36px 15px 50px;
    }

  .columns-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 30px;

    font-family: Raleway;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    letter-spacing: 0.05em;

    @media screen and (min-width: 768px) and (max-width: 1280px) {
            grid-column-gap: 15px;
            font-size: 16px;
            line-height: 26px;
    }

    @media screen and (max-width:767px){
        display: block;
        font-size: 14px;
        line-height: 22px;
    }

    .col-1 > div,.col-2 > div {
      margin-bottom: 20px;
      position: relative;
      margin-left: 10px;

      &::before{
        content: '';
        height: 5px;
        width: 5px;
        background-color: #916C58;
        position: absolute;
        top: 12px;
        left: -10px;

        @media screen and (max-width:767px){
            top: 7px;
        }
      }



      span.bold{
        font-weight: 600;
      }
    }



  }

  .link-1{
    align-self: flex-end;
     @media screen and (max-width:767px){
        font-size: 10px;
    }
  }
}

.techno-gym-container{
    background-image: url("../../assets/images/main-g-techno-gym-background.jpg");
    background-size: cover;
    height: 570px;
    display: flex;
    position: relative;


    @media screen and  (min-width: 768px) and (max-width: 1280px)  {
      height: 460px;
    }


    @media screen and (max-width:767px){
        background-image: url("../../assets/images/main-g-techno-gym-background-320.jpg");
        height: 400px;
    }




    .col-1{

      padding: 116px 0 64px 150px;
      flex-direction: column;
      display: flex;
      justify-content: space-between;
      color: #ffff;

      @media screen and  (min-width: 768px) and (max-width: 1280px)  {
            padding: 50px 0 50px 50px;
      }

        @media screen and (max-width:767px){
            padding: 40px 15px 40px 15px;
        }

      .logo{
        background-image: url("../../assets/images/technogym-logo.svg");
        background-repeat: no-repeat;
        background-size: contain;
        height: 48px;


          @media screen and  (min-width: 768px) and (max-width: 1280px)  {
            height: 28px;
          }
          @media screen and (max-width:767px){
            height: 23px;

        }

      }

      .title{
          font-family: Oswald;
          font-weight: 600;
          font-size: 64px;
          letter-spacing: 0.02em;
          font-feature-settings: 'pnum' on, 'lnum' on;
          color: #916C58;

          @media screen and  (min-width: 768px) and (max-width: 1280px)  {
            font-size: 45px;
          }
          @media screen and (max-width:767px){
            font-size: 36px;
            // padding: 50px 0 30px 0;
          }
      }

      .text{
        font-family: Raleway;
        font-weight: bold;
        font-size: 18px;
        line-height: 32px;
        letter-spacing: 0.1em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #FFFFFF;

         @media screen and  (min-width: 768px) and (max-width: 1280px)  {
              font-size: 16px;
              line-height: 26px;
         }
          @media screen and (max-width:767px){
              font-size: 14px;
              line-height: 22px;
          }
      }

      .link-2{
        margin-top: 10px;


        // @media screen and  (min-width: 768px) and (max-width: 1280px)  {
        //   margin-top: 20px;
        // }

      }

    }

    img{
      height: 294px;
      align-self: flex-end;
      margin-bottom: -60px;

      @media screen and  (min-width: 768px) and (max-width: 1280px)  {
          height: 189px;
          margin-bottom: -32px;
      }

      @media screen and (max-width:767px){
        height: 105px;
        margin-bottom: initial;
        position: absolute;
        right: 0;
        bottom: -30px;
      }
    }

}
