    .also-for-you-container{
      padding: 90px 150px 60px;
      background-color: #fff;

      @media screen and (max-width:767px){
        padding: 60px 30px;
      }

      &>.title{
        font-family: Oswald;
        font-size: 48px;
        font-weight: 600;
        letter-spacing: 0.02em;
        text-align: center;
        margin-bottom: 62px;

        @media screen and (max-width:767px){
          font-size: 24px;
          line-height: 36px
        }
      }

      .orders{
        display: grid;
        grid-column-gap:75px;
        grid-template-columns: repeat(3, 1fr);

        @media screen and (max-width:767px){
          grid-template-columns: repeat(1, 1fr);
          grid-row-gap: 15px;
        }

        .order{

          .icon{
            height: 67px;
          }

          .title{
            font-family: Oswald;
            font-size: 28px;
            line-height: 54px;
            font-weight: 600;
            letter-spacing: 0.02em;
            margin: 12px 0 10px;
          }

          .description{
            font-family: Raleway;
            font-size: 18px;
            line-height: 32px;
            letter-spacing: 0.05em;
            font-feature-settings: 'pnum' on, 'lnum' on;

            @media screen and (max-width:767px){
                font-size: 14px;
                line-height: 22px;
            }
          }

          &:nth-of-type(1) .icon {
            background:url('../../assets/images/skew-icon-1.svg') no-repeat left, url('../../assets/images/smoothie.svg') no-repeat 60px;
          }

          &:nth-of-type(2) .icon {
            background: url('../../assets/images/skew-icon-1.svg') no-repeat left, url('../../assets/images/heart-beat.svg') no-repeat 60px;
          }

          &:nth-of-type(3) .icon {
            background: url('../../assets/images/skew-icon-1.svg') no-repeat left, url('../../assets/images/hanger.svg') no-repeat 60px;
          }

        }

      }

    }

